section {
  scroll-margin-top: 100px;
}

span {
  color: #38bdf8;
}

/** Media Queries **/
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.hero {
  background: linear-gradient(135deg, #06196e 0%, #0c1b3f 50%, #041975 100%);
  border: none;
  text-align: center;
  padding: 2rem;
  margin-top: 2rem;
}
.hero h1 {
  margin-bottom: 2.5rem;
}
@media (min-width: 769px) {
  .hero h1 {
    font-size: 5rem;
  }
}
.hero span {
  color: #38bdf8;
}
.hero p {
  font-size: 1.3rem;
}
@media (min-width: 769px) {
  .hero p {
    padding: 0 5rem;
  }
}
@media (min-width: 769px) {
  .hero p {
    text-align: justify;
  }
}
.hero h2 {
  color: #94a3b8;
  font-size: 0.8rem;
}

.opciones {
  margin: 1rem;
  padding: 1rem;
  justify-content: space-around;
}
@media (min-width: 769px) {
  .opciones {
    display: flex;
  }
}
.opciones .cv {
  background-color: #001c58;
  color: white;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 5/2;
  display: block;
  border: none;
  border-radius: 9%;
  margin: 1rem;
}
.opciones .cv:hover {
  background-color: rgb(0, 19.8863636364, 62.5);
  cursor: pointer;
}
@media (min-width: 769px) {
  .opciones .cv {
    margin: 0;
  }
}
.opciones .cv i {
  color: rgb(0, 180, 216, var(--tw-text-opacity, 1));
}
.opciones .cv a {
  text-decoration: none;
  color: #38bdf8;
}
.opciones .ir_proyecto {
  background-color: rgba(0, 28, 88, 0.9450980392);
  color: white;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 5/2;
  display: block;
  border: none;
  border-radius: 9%;
  color: #38bdf8;
  text-decoration: none;
}
.opciones .ir_proyecto:hover {
  background-color: rgba(0, 19.8863636364, 62.5, 0.9450980392);
  cursor: pointer;
}